.FinishedQuiz {
	padding: 20px;
	color: #fff;
	border: 2px solid #fff;
	border-radius: 5px;
	box-sizing: border-box;
	margin: 0 10px;
}
.FinishedQuiz p {
	font-size: 2rem;
	margin-bottom: 1rem;
}

.FinishedQuiz ul {
	list-style: none;
	padding: 0;
	margin: 0;
	font-size: 2rem;
}
.FinishedQuiz ul li i {
	margin-left: 10px;
	font-size: 2rem;
}
.FinishedQuiz .fa-check {
	color: #82BF56;
}
.FinishedQuiz .fa-times {
	color: #E74C3C;
}