.QuizList {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-top: 100px;
	flex-grow: 1;
	width: 100%;
}
.QuizList h1 {
	color: #000;
	font-size: 5rem;
}
.QuizList ul {
	margin: 0;
	padding: 0;
	list-style: none;
}
.QuizList ul li {
	margin-bottom: 10px;
}
.QuizList ul li a {
	text-decoration: none;
	color: #000;
	font-weight: bold;
	font-size: 3rem;
}
.QuizList ul li a:hover {
	color: #363f54;
}